import { getPowerApplicationInProgressCount } from '@/api/powerApplication';
import CommonCardFrame from '@/components/CommonCardFrame';
export default {
  components: {
    CommonCardFrame: CommonCardFrame
  },
  filters: {
    title: function title(val) {
